import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const state = {
	// 登录模式
	pattern: {
		name: "",
		type: ""
	},
	// 控制导航样式
	navigation: true,
	// 用户信息
	user: {
		accounts: "",
		password: "",
	}
};

const mutations = {
	setPattern(state, val) {
		state.pattern = val;
	},
	setNavigation(state, val) {
		state.navigation = val;
	},
	setUser(state, val) {
		state.user = val;
	}
};

const actions = {
	setPattern({commit}, val) {
		commit("setPattern", val);
	},
	setNavigation({commit}, val) {
		commit("setNavigation", val);
	},
	setUser({commit}, val) {
		commit("setUser", val);
	}
};

export default new Vuex.Store({
	state,
	mutations,
	actions
});
