<template>
  <div class="m-header">
    <transition name="slide-fade">
      <el-row v-if="show">
        <el-col class="item" :span="14">
          <guide />
        </el-col>
        <el-col class="item" :span="5">
          <search />
        </el-col>
        <el-col class="item" :span="5">
          <logon/>
        </el-col>
      </el-row>
    </transition>
    <el-row>
      <navigation v-if="this.$store.state.navigation" />
    </el-row>
  </div>
</template>

<script>
import guide from "./guide.vue";
import search from "./search.vue";
import logon from "./logon.vue";
import navigation from "./navigation.vue";
export default {
  data() {
    return {
      show: true
    };
  },
  methods: {
    handleScroll() {
      let scrollTop =
        document.body.scrollTop || document.documentElement.scrollTop;
      let direction = scrollTop > 200 ? "up" : "down";
      if (direction === "up") {
        this.show = false;
      } else {
        this.show = true;
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll, true);
  },
  components: {
    guide,
    search,
    logon,
    navigation
  }
};
</script>

<style lang="scss">
@import "../../assets/css/header/index.scss";
</style>
