<template>
	<div class="m-reply">
		<el-row class="replyDiv">
			<el-col :span="span1" :push="retract">
				<el-col :span="2" class="headPortrait" v-if="headPortrait">
					<img src="https://sf1-ttcdn-tos.pstatp.com/img/mosaic-legacy/3791/5070639578~120x256.image" alt="头像">
				</el-col>
				<el-col :span="span2">
					<VueEmoji @input="selectEmoji" />
					<div class="showPictureDiv" v-show="pictureData">
						<img src='' alt='图片' class='showPicture' />
						<span class="deleteImage" @click="deleteSelectImage">X</span>
					</div>
					<div class="option clearfix">
						<div class="pictureDiv" v-show="headPortrait">
							<i class="el-icon-picture"></i>
							<input type="file" class="pictureFile" title="" @input="selectPicture">
						</div>
						<el-button size="mini" type="primary" class="comment" @click="checkComment">评论</el-button>
					</div>
				</el-col>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import VueEmoji from 'emoji-vue'
	export default {
		data() {
			return {
				comment: '',
				span1: 24 - this.retract,
				span2: this.headPortrait ? 22 : 24,
				pictureData: ''
			}
		},
		props: [
			'headPortrait',
			'retract',
			'reply',
		],
		components: {
			VueEmoji
		},
		methods: {
			checkComment() {
				let content = this.comment;
				if (!content) {
					alert("请输入评论内容")
					return;
				}
				// 提交评论及相关图片
			},
			selectPicture() {
				let picture = document.getElementsByClassName('pictureFile')[0];
				let emoji = document.getElementsByClassName('emoji-picker-icon')[0];
				let image = document.getElementsByClassName('showPicture')[0];
				let file = picture.files[0];
				let reader = new FileReader();
				if (file.type.indexOf('image/jpeg') != 0 || this.pictureData != '') return;
				reader.readAsDataURL(file);
				reader.onload = function(e) {
					let newUrl = this.result;
					image.src = newUrl;
				};
				this.pictureData = picture.files[0].name;
				emoji.classList.add('selectPicture');
			},
			selectEmoji(emoji) {
				this.comment = emoji;
				console.log(emoji)
			},
			deleteSelectImage() {
				let picture = document.getElementsByClassName('pictureFile')[0];
				let emoji = document.getElementsByClassName('emoji-picker-icon')[0];
				picture.value = '';
				this.pictureData = '';
				emoji.classList.remove('selectPicture');
			}
		}
	}
</script>

<style lang="scss">
	@import "../../../assets/css/details/reply.scss";
</style>
