<template>
  <div>
    <el-row>
      <el-col>
        <el-form :model="pwdList" status-icon :rules="rules" ref="pwdList">
          <el-form-item
            label-width="80px"
            prop="eMail"
            label="邮箱"
            :rules="[
              { required: true, message: '请输入邮箱地址', trigger: 'blur' },
              {
                type: 'email',
                message: '请输入正确的邮箱地址',
                trigger: ['blur', 'change']
              }
            ]"
          >
            <el-input v-model="pwdList.eMail"></el-input>
          </el-form-item>
          <el-form-item label-width="80px" label="密码" prop="pass">
            <el-input
              type="password"
              v-model="pwdList.newPassWord"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label-width="80px" label="确认密码" prop="checkPass">
            <el-input
              type="password"
              v-model="pwdList.repeatPassWord"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              class="submit"
              type="primary"
              @click="submitForm('pwdList')"
              >提交</el-button
            >
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    let validatePass = (rule, value, callback) => {
      if (
        this.pwdList.newPassWord === "" ||
        this.pwdList.newPassWord === undefined
      ) {
        callback(new Error("请输入密码"));
      } else {
        if (this.pwdList.repeatPassWord !== "") {
          this.$refs.pwdList.validateField("checkPass");
        }
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (
        this.pwdList.repeatPassWord === "" ||
        this.pwdList.repeatPassWord === undefined
      ) {
        callback(new Error("请再次输入密码"));
      } else if (this.pwdList.repeatPassWord !== this.pwdList.newPassWord) {
        console.log("newPassWord", this.pwdList.newPassWord);
        console.log("repeatPassWord", this.pwdList.repeatPassWord);
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      rules: {
        pass: [
          {
            validator: validatePass,
            trigger: "blur"
          }
        ],
        checkPass: [
          {
            validator: validatePass2,
            trigger: "blur"
          }
        ]
      }
    };
  },
  props: ["pwdList"],
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("提交");
        } else {
          console.log("提交失败");
          return false;
        }
      });
    }
  }
};
</script>

<style></style>
