<template>
	<div class="m-aside">
		<el-row>
			<el-col :span="24" class="title_one">掘金 - juejin.im</el-col>
		</el-row>
		<el-row>
			<el-col :span="24" class="title_two">一个帮助开发者成长的社区</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-input type="text" class="username" placeholder="帐号" prefix-icon="el-icon-user" v-model="userName"></el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
