				<span class="forget" @click="openLogon({ name: '重置密码', type: 'reset' })">忘记密码</span>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				userName: "",
				password: ""
			};
		},
		methods: {
			openLogon(value) {
				this.$store.commit("setPattern", {
					name: value.name,
					type: value.type
				});
				console.log(this.$store.state);
				document.body.style.overflow = "hidden";
			}
		}
	};
</script>

