<template>
	<div class="m-login">
		<div class="m-button-group">
			<router-link to="/write">
				<el-button class="write" size="mini">写文章</el-button>
			</router-link>
			<div class="more" @click="moreShow" v-document-click="documentClick">
				<i class="el-icon-caret-bottom"></i>
			</div>
			<ul v-if="more">
				<li>发布沸点</li>
			</ul>
		</div>
		<el-button class="login" size="medium" @click="logon" v-if="this.$store.state.user.accounts == '' && this.$store.state.user.password == ''">登录</el-button>
		<div v-else class="userDiv" @mouseover="focusUser" @mouseout="blurUser">
			<img src="https://sf6-ttcdn-tos.pstatp.com/img/mosaic-legacy/3791/5070639578~120x256.image" alt="头像">
			<ul v-show="showUserList">
				<li @click="openUserList">个人主页</li>
				<li>退出</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				more: false,
				showUserList: false,
			};
		},
		methods: {
			moreShow(e) {
				e.stopPropagation();
				if (this.more === false) this.more = true;
				else this.more = false;
			},
			documentClick() {
				this.more = false;
			},
			logon() {
				this.$store.commit("setPattern", {
					name: "登录",
					type: "logon"
				});
				document.body.style.overflow = "hidden";
			},
			publishArticle() {
				console.log("发布文章");
			},
			focusUser() {
				this.showUserList = true;
			},
			blurUser() {
				this.showUserList = false;
			},
			openUserList() {
				this.$router.push('await')
			}
		}
	};
</script>

<style scoped lang="scss">
	@import "../../assets/css/header/logon.scss";
</style>
