<template>
	<div class="m-content" @mouseenter="showShare" @mouseleave="hideShare" @click="showContent">
		<el-row>
			<el-col :span="24">
				<el-breadcrumb separator-class="el-icon-arrow-right">
					<el-breadcrumb-item :to="{ path: 'cList.address' }">
						{{cList.author}}
					</el-breadcrumb-item>
					<el-breadcrumb-item>{{ cList.time }}小时</el-breadcrumb-item>
					<el-breadcrumb-item v-for="(list, i) in cList.classifys" :key="i">
						<a :href="'list.address'">{{ list.name }}</a>
					</el-breadcrumb-item>
				</el-breadcrumb>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="22" class="titleTab">
				<el-row>
					<el-col :span="24">
						<span class="title" :to="'cList.address'">{{ cList.title }}</span>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="24">
						<el-button-group>
							<el-button @click="like" :class="{ changeLike: toLike }" size="mini" class="dianzan" icon="el-icon-thumb">{{ cList.like }}</el-button>
							<el-button @click="showComment" size="mini" icon="el-icon-chat-dot-round">{{ cList.comment }}</el-button>
							<el-button @click="clickShare" v-if="share" size="mini" icon="el-icon-share"></el-button>
						</el-button-group>
					</el-col>
				</el-row>
			</el-col>
			<el-col :span="2">
				<img v-if="cList.image" class="img" :src="cList.image" alt="图片" />
			</el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				toLike: false,
				share: false
			};
		},
		props: ["cList"],
		methods: {
			showShare() {
				this.share = true;
			},
			clickShare(e) {
				e.stopPropagation();
			},
			hideShare() {
				this.share = false;
			},
			showComment(e) {
				e.stopPropagation();
				this.$router.push("content/" + this.cList.content + "#comment");
				this.$store.commit("setNavigation", false);
			},
			showContent() {
				if (this.$store.state.user.accounts == '' && this.$store.state.user.password == '') {
					alert("请先登录账号");
					return;
				}
				this.$router.push("content/" + this.cList.content);
				this.$store.commit("setNavigation", false);
			},
			like(e) {
				e.stopPropagation();
				if (this.toLike) {
					this.toLike = false;
					this.cList.like--;
				} else {
					this.toLike = true;
					this.cList.like++;
				}
			}
		}
	};
</script>

<style lang="scss">
	@import "../../assets/css/index/content.scss";
</style>
