<template>
	<div class="m-write">
		<div class="titleDiv">
			<el-row>
				<el-col :span="22">
					<el-input v-model="content.titel" placeholder="输入文章标题..." class="title"></el-input>
				</el-col>
				<el-col :span="2">
					<div class="pictureButton" @click="showDiv"><i class="el-icon-picture"></i></div>
					<div class="pictureDiv" v-show="show">
						<div class="arrow-top arrow-box">
							<span class="top">
								<i class="top-arrow1"></i>
								<i class="top-arrow2"></i>
							</span>
						</div>
						<div class="imgDiv">
							<span>添加封面大图</span>
							<span class="deletePicture" @click="deletePicture"><i class="el-icon-delete"></i></span>
							<div>
								<span @click="clickPicture" id="picture">点击此处添加图片</span>
								<img src="" alt="图片" id="pictureDiv"></span>
								<input type="file" id="pictureFile" title="" @change="selectPicture">
							</div>
						</div>
					</div>
				</el-col>
			</el-row>
		</div>
		<mavon-editor v-model="content.value" @imgAdd="$imgAdd" @imgDel="$imgDel" @save="$save" />
	</div>
</template>

<script>
	export default {
		data() {
			return {
				content: {
					title: '',
					cover: '',
					value: '',
				},
				show: false,
			}
		},
		methods: {
			$save() {
				console.log("保存内容", this.value);
			},
			$imgAdd() {
				console.log("上传图片");
			},
			$imgDel() {
				console.log("移除图片");
			},
			showDiv() {
				if (this.show) this.show = false;
				else this.show = true;
			},
			clickPicture() {
				document.getElementById('pictureFile').click();
			},
			selectPicture() {
				let deletePicture = document.getElementsByClassName('deletePicture')[0];
				let pictureButton = document.getElementsByClassName('pictureButton')[0];
				let picture = document.getElementById('picture');
				let pictureDiv = document.getElementById('pictureDiv');
				let pictureFile = document.getElementById('pictureFile');
				let reader = new FileReader();
				reader.readAsDataURL(pictureFile.files[0]);
				reader.onload = () => {
					pictureDiv.src = reader.result;
					pictureDiv.style.cssText = 'display: inline; width: 234px; height: 100px;';
					pictureButton.style.color = '#a5ebff';
					picture.style.display = 'none';
					deletePicture.style.display = 'inline';
				};
			},
			deletePicture () {
				let pictureFile = document.getElementById('pictureFile');
				let pictureDiv = document.getElementById('pictureDiv');
				let picture = document.getElementById('picture');
				let deletePicture = document.getElementsByClassName('deletePicture')[0];
				let pictureButton = document.getElementsByClassName('pictureButton')[0];
				this.content.cover = '';
				pictureFile.value = '';
				pictureDiv.src = '';	
				picture.style.display = 'block';
				pictureDiv.style.display = 'none'
				deletePicture.style.display = 'none';
				pictureButton.style.color = '#ebebeb';			
			}
		}
	};
</script>

<style lang="scss">
	@import "../assets/css/write/index.scss"
</style>
