<template>
	<div class="m-logon">
		<div class="shell">
			<!-- 标题-->
			<el-row class="titleTab">
				<el-col>
					<span class="close" @click="closeWindow">X</span>
					<span class="title">{{ genre.name }}</span>
				</el-col>
			</el-row>
			<!-- 忘记密码 -->
			<el-row v-if="genre.type === 'reset'">
				<el-col style="height: 230px;">
					<myReset :pwdList="resetPwdList" />
				</el-col>
			</el-row>
			<!-- 注册 -->
			<el-row v-else-if="genre.type === 'register'">
				<el-col style="height: 300px;">
					<myRegister :ruList="registerUserList" />
				</el-col>
			</el-row>
			<!-- 登录 -->
			<el-row v-else>
				<el-col>
					<myLogin :loginList="logonList" />
				</el-col>
			</el-row>
			<el-row>
				<el-col>
					<span v-show="!(genre.type === 'reset')" class="forget left" @click="openResetPwd">忘记密码</span>
					<span v-show="!(genre.type === 'register')" class="register right" @click="openRegister">立即注册</span>
					<span v-show="!(genre.type === 'logon')" class="login" @click="openLogin" :class="{
						left: genre.type === 'reset', right: genre.type === 'register'
					}">马上登录</span>
				</el-col>
			</el-row>
		</div>
	</div>
</template>

<script>
	import myReset from "./reset.vue";
	import myLogin from "./login.vue";
	import myRegister from "./register.vue";
	export default {
		data() {
			return {
				genre: this.$store.state.pattern,
				logonList: {
					username: "",
					password: "",
					remember: false
				},
				resetPwdList: {
					eMail: "",
					newPassWord: "",
					repeatPassWord: ""
				},
				registerUserList: {
					userName: "",
					passWord: "",
					checkPass: "",
					eMail: ""
				}
			};
		},
		components: {
			myRegister,
			myLogin,
			myReset
		},
		watch: {
			"$store.state.pattern"() {
				this.genre = this.$store.state.pattern;
			}
		},
		methods: {
			openResetPwd() {
				this.$store.commit("setPattern", {
					name: "重置密码",
					type: "reset"
				});
			},
			openRegister() {
				this.$store.commit("setPattern", {
					name: "注册帐号",
					type: "register"
				});
			},
			openLogin() {
				this.$store.commit("setPattern", {
					name: "登录",
					type: "logon"
				});
			},
			closeWindow() {
				this.$store.commit("setPattern", {
					name: ""
				});
				document.body.style.overflow = "";
			}
		}
	};
</script>

<style lang="scss">
	@import "../../assets/css/logon/index.scss";
</style>
