import Vue from "vue";
import Router from "vue-router";
import store from "../store.js";
import defaultPage from "../layout/default.vue";
import index from "../page/index.vue";
import detail from "../page/details.vue";
import write from "../page/write.vue";
import Await from "../page/await.vue";

Vue.use(Router);

const router = new Router({
	mode: "hash",
	routes: [{
		path: "/",
		name: "default",
		component: defaultPage,
		redirect: "/index",
		children: [{
				path: "/index",
				name: "index",
				component: index
			},
			{
				path: "content/:name",
				name: "detail",
				component: detail
			}
		]
	}, {
		path: "/write",
		name: "write",
		component: write
	}, {
		path: '/await',
		name: 'await',
		component: Await
	}]
});

// 路由守卫
router.beforeEach((to, from, next) => {
	// console.log("to", to);
	// console.log("from", from);
	if (to.path == "await") next('Await');
	if (to.name === "index") {
		store.commit("setNavigation", true);
	} else {
		store.commit("setNavigation", false);
	}
	next();
});
export default router;
