<template>
  <div>
    <el-form :model="ruList" status-icon :rules="rules" ref="ruList">
      <el-form-item label-width="80px" label="用户名">
        <el-input
          type="text"
          v-model="ruList.userName"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label-width="80px" label="密码" prop="pass">
        <el-input
          type="password"
          v-model="ruList.passWord"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label-width="80px" label="确认密码" prop="checkPass">
        <el-input
          type="password"
          v-model="ruList.checkPass"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item
        label-width="80px"
        prop="eMail"
        label="邮箱"
        :rules="[
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          {
            type: 'email',
            message: '请输入正确的邮箱地址',
            trigger: ['blur', 'change']
          }
        ]"
      >
        <el-input v-model="ruList.eMail"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="submit" type="primary" @click="submitForm('ruList')"
          >提交</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    let validatePass = (rule, value, callback) => {
      if (this.ruList.passWord === "" || this.ruList.passWord === undefined) {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruList.checkPass !== "") {
          this.$refs.ruList.validateField("checkPass");
        }
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (this.ruList.checkPass === "" || this.ruList.checkPass === undefined) {
        callback(new Error("请再次输入密码"));
      } else if (this.ruList.checkPass !== this.ruList.passWord) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      rules: {
        pass: [
          {
            validator: validatePass,
            trigger: "blur"
          }
        ],
        checkPass: [
          {
            validator: validatePass2,
            trigger: "blur"
          }
        ]
      }
    };
  },
  props: ["ruList"],
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("提交");
        } else {
          console.log("提交失败");
          return false;
        }
      });
    }
  }
};
</script>

<style></style>
