<template>
  <div class="m-choice">
    <ul class="clearfix">
      <li :class="{ select: select == 'all' }" @click="select = 'all'">全部</li>
      <template v-for="(item, index) in list">
        <li
          :class="{ select: select == item.name + '_' + index }"
          @click="select = item.name + '_' + index"
          v-if="index < 10"
          :key="index"
        >
          {{ item.name }}
        </li>
        <li
          :class="{ select: select == item.name + '_' + index }"
          @click="select = item.name + '_' + index"
          v-else-if="index > 10 && more"
          :key="index"
        >
          {{ item.name }}
        </li>
        <li
          v-else-if="more == false && index == 10"
          :key="'more' + index"
          @click="openMore"
        >
          展开<i class="el-icon-caret-bottom"></i>
        </li>
      </template>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      select: "all",
      more: false,
      list: [
        {
          name: "Java"
        },
        {
          name: "后端"
        },
        {
          name: "Spring Boot"
        },
        {
          name: "Python"
        },
        {
          name: "Go"
        },
        {
          name: "Spring"
        },
        {
          name: "MySQL"
        },
        {
          name: "数据库"
        },
        {
          name: "Redis"
        },
        {
          name: "算法"
        },
        {
          name: "架构"
        },
        {
          name: "JVM"
        },
        {
          name: "服务器"
        },
        {
          name: "Linux"
        },
        {
          name: "PHP"
        }
      ]
    };
  },
  methods: {
    openMore() {
      this.more = true;
    }
  }
};
</script>

<style lang="scss">
@import "../../assets/css/index/choice.scss";
</style>
