<template>
	<div class="m-commentContent">
		<el-row>
			<el-col :span="push" :push="retract">
				<el-col :span="2" class="headPortrait">
					<img :src="comment.userHeadPortrait" alt="头像">
				</el-col>
				<el-col :span="22">
					<div class="userDiv">
						<span class="name">{{comment.userName}}</span>
						<span class="level">Lv{{comment.level}}</span>
						<span class="tag">{{comment.tag}}</span>
					</div>
					<div class="commentContent">
						{{comment.content}}
					</div>
					<div class="optionDiv clearfix">
						<span class="time">{{comment.commentTime}}</span>
						<div>
							<el-button size="mini" class="like" @click="like">点赞<span>{{comment.like}}</span></el-button>
							<el-button size="mini" icon="el-icon-chat-round" class="reply" @click="reply">回复</el-button>
						</div>
					</div>
				</el-col>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				toLike: false,
				showReply: false,
				push: 24 - this.retract,
			}
		},
		props: [
			'comment', 'retract'
		],
		methods: {
			reply() {
				if (!this.showReply) this.showReply = true;
				else this.showReply = false;
				let num = this.comment.userId;
				this.$emit('listenShowReply', {
					answer: this.showReply,
					value: num
				});
			},
			like() {
				if (this.toLike) {
					this.toLike = false;
					this.comment.like--;
				} else {
					this.toLike = true;
					this.comment.like++;
				}
			}
		},
	};
</script>

<style lang="scss">
	@import '../../../assets/css/details/commentContent.scss'
</style>
