<template>
	<div>
		<el-row>
			<el-col>
				<el-input v-model="loginList.username" class="username" type="text" placeholder="帐号" prefix-icon="el-icon-user"></el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col>
				<el-input v-model="loginList.password" class="password" type="password" placeholder="密码" prefix-icon="el-icon-lock"></el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col>
				<el-button type="primary" class="submit" @click="logIn">立即登录</el-button>
			</el-col>
		</el-row>
		<el-row>
			<el-col>
				<span class="remember">
					<el-checkbox v-model="loginList.remember">记住帐号密码</el-checkbox>
				</span>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import cookie from '../../tool/cookieTool.js';
	import api from '../../api/api.js';
	export default {
		props: ["loginList"],
		methods: {
			logIn() {
				api.login().then(res => {
					let accounts = this.loginList.username;
					let password = this.loginList.password;
					if (res.data.accounts == accounts && res.data.password == password) {
						// 把用户信息存入session或cookie更改登录样式并关闭登入窗口
						if (this.loginList.remember) {
							cookie.setCookie('accounts', accounts, 120);
							cookie.setCookie('password', password, 120);
						}
						this.$store.commit("setUser", {
							accounts: accounts,
							password: password
						});
						this.$store.commit("setPattern", {
							name: ""
						});
						document.body.style.overflow = "";
						console.log(this.$store.state.user)
					} else {
						alert("帐号或密码不正确，登入失败。")
					}
				})
			}
		}
	};
</script>

<style></style>
